import Product from "./Product";
import AddProduct from "./AddProduct";
import EditProduct from "./EditProduct";
import Order from "./Order";
import EditOrder from "./EditOrder";
import Error from "../Error";
import Dashboard from "../Admin/Dashboard";
import Profile from "./Profile";

const router = {
  routers: [
    {
      url: "/provider/dashboard",
      component: Dashboard,
      header: "Trang ch"
    },
    {
      url: "/provider/product/list",
      component: Product,
      header: "Danh sch sn phm"
    },
    {
      url: "/provider/product/add",
      component: AddProduct,
      header: "Thm sn phm"
    },
    {
      url: "/provider/product/edit",
      component: EditProduct,
      header: "Chnh sa thng tin sn phm"
    },
    {
      url: "/provider/order/list",
      component: Order,
      header: "Danh sch n hng"
    },
    {
      url: "/provider/order/edit",
      component: EditOrder,
      header: "Chnh sa thng tin n hng"
    },
    {
      url: "/provider/profile",
      component: Profile,
      header: "Chnh sa thng ti khon"
    }
  ],
  getRoute: function(url) {
    let router = this.routers.find(e => url === e.url);
    console.log(router);
    return router ? router : { component: Error, header: "404", code: 404 };
  }
};

export default router;
